import {Registry} from 'cs-web-components-base';
import ErrorWidget from './ErrorWidget.jsx';
import TextNoteWidget from './TextNoteWidget.jsx';
import ObjectWidget from './ObjectWidget.jsx';
import ObjectListWidget from './ObjectListWidget.jsx';
import SearchFavourite from './SearchFavourite';
import OnboardingWidget from './OnboardingWidget';

Registry.registerComponent(`${componentNameSpace}-TextNoteWidget`, TextNoteWidget);
Registry.registerComponent(`${componentNameSpace}-ObjectWidget`, ObjectWidget);
Registry.registerComponent(`${componentNameSpace}-SearchFavourite`, SearchFavourite);
Registry.registerComponent(`${componentNameSpace}-OnboardingWidget`, OnboardingWidget);

export default {
    ErrorWidget,
    ObjectListWidget,
    SearchFavourite,
    OnboardingWidget
};



