import {Registry} from 'cs-web-components-base';
import Dashboard from './Dashboard.jsx';
import DashboardItem from './DashboardItem.jsx';
import {DASHBOARD_STORE} from './constants.js';
import {setSettings} from './actions.js';
import reducer from './reducers.js';
import Widgets from './widgets';

Registry.registerComponent(`${componentNameSpace}-Dashboard`, Dashboard);
Registry.registerReducer(DASHBOARD_STORE, reducer);

export default {
    DashboardItem,
    setSettings,
    ObjectListWidget: Widgets.ObjectListWidget
};



